import React from 'react';
import { Link } from 'gatsby';
import {
  Highlight,
  Hits,
  Index,
  Snippet,
  PoweredBy,
  useStats,
  useInfiniteHits,
} from 'react-instantsearch';
import { StyledSearchResults } from './styled';

const HitCount = () => {
  const { nbHits } = useStats();

  return nbHits > 0 ? (
    <div className="HitCount">
      {nbHits} result{nbHits !== 1 ? 's' : ''}
    </div>
  ) : null;
};

const PageHit = ({ hit }) => (
  <div>
    <Link to={hit.path}>
      <h4>
        <Highlight attribute="path" hit={hit} />
      </h4>
    </Link>
    <Snippet attribute="path" hit={hit} />
  </div>
);

const HitsInIndex = ({ indexName }: { indexName: string }) => (
  <Index indexName={indexName}>
    <Hits className="Hits" hitComponent={PageHit} />
  </Index>
);

const SearchResult = () => (
  <StyledSearchResults>
    <HitsInIndex indexName={'thm_website'} />
    <div style={{ width: '150px' }}>
      <PoweredBy />
    </div>
  </StyledSearchResults>
);

export default SearchResult;
